
.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #c2c2c2;
  font-weight: 600;
  text-transform: uppercase;
  overflow-y: auto;

  &__top {
    margin-bottom: 24px;
    top: 0;
    z-index: 1;
  }

  &__nav {
    margin-bottom: 32px;
  }

  &__content {
    @include constentPaddingInLine;
  }

  &__phone-number {
    margin-top: 40px;
    display: block;
    margin-bottom: 16px;
    font-size: 16px;
    line-height: 27px;
    color: inherit;
    text-decoration: none;
  }

  &__call-to-order {
    display: block;
    width: fit-content;
    text-decoration: none;
    padding-bottom: 7px;
    border-bottom: 1px solid $main-text-color ;

    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    color: inherit;
  }
}
/* #endregion */
